.App {
  color: #4a4a4a;

  text-align: center;
}

.game-result-text {
  color: purple;
}

.game-table {
  margin: 20px auto;
  height: 600px;
  width: 800px;

  background: #e4e4e4;
}

.game-shell {
  position: absolute;
  display: -ms-flexbox;
  display: flex;

  width: 100px;
  height: 100px;

  background: #f47100;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  cursor: pointer;
  -webkit-transition: -webkit-transform 700ms;
  transition: -webkit-transform 700ms;
  -o-transition: transform 700ms;
  transition: transform 700ms;
  transition: transform 700ms, -webkit-transform 700ms;
  will-change: transform;
}

.game-ball {
  width: 50px;
  height: 50px;

  border-radius: 50%;
  background: purple;
  opacity: 0;

  -webkit-transition: opacity 500ms;

  -o-transition: opacity 500ms;

  transition: opacity 500ms;
}


